    ])
    
    // ÊúÄËøëËÆøÈóÆÁöÑÈ°µÈù¢Ôºà‰ªélocalStorageËé∑ÂèñÔºâ
    const recentPages = ref([])
    
    // ÊñπÊ≥ï
    const goHome = () => {
      router.push('/')
      notify.info('Â∑≤ËøîÂõûÈ¶ñÈ°µ')
    }
    
    const goBack = () => {
      if (window.history.length > 1) {
        router.go(-1)
      } else {
        router.push('/')
      }
      notify.info('Â∑≤ËøîÂõû‰∏ä‰∏ÄÈ°µ')
    }
    
    const reportIssue = () => {
      const currentUrl = window.location.href
      const subject = encodeURIComponent('È°µÈù¢ËÆøÈóÆÈóÆÈ¢òÊä•Âëä')
      const body = encodeURIComponent(`ÊàëÂú®ËÆøÈóÆ‰ª•‰∏ãÈ°µÈù¢Êó∂ÈÅáÂà∞‰∫Ü404ÈîôËØØÔºö\n\nÈ°µÈù¢URL: ${currentUrl}\nÊó∂Èó¥: ${new Date().toLocaleString()}\n\nËØ∑Â∏ÆÂä©Ëß£ÂÜ≥Ê≠§ÈóÆÈ¢ò„ÄÇ`)
      
      // ÂèØ‰ª•ÊâìÂºÄÈÇÆ‰ª∂ÂÆ¢Êà∑Á´ØÊàñËÄÖË∑≥ËΩ¨Âà∞ËÅîÁ≥ªÈ°µÈù¢
      router.push({
        path: '/contact',
        query: {
          subject: 'È°µÈù¢ËÆøÈóÆÈóÆÈ¢ò',
          message: `È°µÈù¢URL: ${currentUrl}`
