      notify.info('Â∑≤Ë∑≥ËΩ¨Âà∞ËÅîÁ≥ªÈ°µÈù¢ÔºåÊÇ®ÂèØ‰ª•Âú®ÈÇ£ÈáåÊä•ÂëäÈóÆÈ¢ò')
    }
    
    // Ëé∑ÂèñÊúÄËøëËÆøÈóÆÁöÑÈ°µÈù¢
    const loadRecentPages = () => {
      try {
        const recent = localStorage.getItem('recent-pages')
        if (recent) {
          const pages = JSON.parse(recent)
          recentPages.value = pages.slice(0, 3) // Âè™ÊòæÁ§∫ÊúÄËøë3‰∏™
        }
      } catch (error) {
        console.warn('Êó†Ê≥ïÂä†ËΩΩÊúÄËøëËÆøÈóÆÁöÑÈ°µÈù¢:', error)
      }
    }
    
    // ÁîüÂëΩÂë®Êúü
    onMounted(() => {
      loadRecentPages()
      
      // ËÆ∞ÂΩï404ÈîôËØØ
      console.warn('404È°µÈù¢ËÆøÈóÆ:', window.location.href)
      
