import { computed } from 'vue'
import { getGlobalAppStore } from '../stores/app.js'

/**
 * ÈÄöÁü•Á≥ªÁªüÁªÑÂêàÂºèÂáΩÊï∞
 * Êèê‰æõ‰æøÊç∑ÁöÑÈÄöÁü•ÁÆ°ÁêÜÂäüËÉΩ
 */
export function useNotifications() {
  const store = getGlobalAppStore()
  
  // ËÆ°ÁÆóÂ±ûÊÄß
  const notifications = computed(() => store.state.notifications)
  const unreadCount = computed(() => store.unreadNotifications.value)
  const hasUnread = computed(() => unreadCount.value > 0)
  
  // ‰æøÊç∑ÁöÑÈÄöÁü•ÊñπÊ≥ï
  const notify = {
    // ÊàêÂäüÈÄöÁü•
    success: (message, options = {}) => {
      return store.addNotification({
        type: 'success',
        title: 'ÊàêÂäü',
        message,
        duration: 3000,
        ...options
      })
    },
    
    // ÈîôËØØÈÄöÁü•
    error: (message, options = {}) => {
      return store.addNotification({
        type: 'error',
        title: 'ÈîôËØØ',
        message,
        duration: 5000,
        ...options
      })
    },
    
    // Ë≠¶ÂëäÈÄöÁü•
    warning: (message, options = {}) => {
      return store.addNotification({
        type: 'warning',
        title: 'Ë≠¶Âëä',
        message,
        duration: 4000,
        ...options
      })
    },
    
    // ‰ø°ÊÅØÈÄöÁü•
    info: (message, options = {}) => {
      return store.addNotification({
        type: 'info',
        title: 'ÊèêÁ§∫',
        message,
        duration: 3000,
        ...options
      })
    },
    
    // Ëá™ÂÆö‰πâÈÄöÁü•
    custom: (notification) => {
      return store.addNotification(notification)
    }
  }
  
  // ÊâπÈáèÊìç‰Ωú
  const batch = {
    // Ê†áËÆ∞ÊâÄÊúâ‰∏∫Â∑≤ËØª
    markAllAsRead: () => {
      notifications.value.forEach(notification => {
        if (!notification.read) {
          store.markNotificationAsRead(notification.id)
        }
      })
    },
    
    // Ê∏ÖÈô§ÊâÄÊúâÂ∑≤ËØª
    clearRead: () => {
      store.clearReadNotifications()
    },
    
    // Ê∏ÖÈô§ÊâÄÊúâ
    clearAll: () => {
      store.clearAllNotifications()
    },
    
    // Ê∏ÖÈô§ÊåáÂÆöÁ±ªÂûã
    clearByType: (type) => {
      const toRemove = notifications.value
        .filter(n => n.type === type)
        .map(n => n.id)
      
      toRemove.forEach(id => store.removeNotification(id))
    }
  }
  
  // ËøáÊª§Âô®
  const filter = {
    // ÊåâÁ±ªÂûãËøáÊª§
    byType: (type) => {
      return computed(() => notifications.value.filter(n => n.type === type))
    },
    
    // Êú™ËØªÈÄöÁü•
    unread: computed(() => notifications.value.filter(n => !n.read)),
    
    // Â∑≤ËØªÈÄöÁü•
    read: computed(() => notifications.value.filter(n => n.read)),
    
    // ÊúÄËøëÁöÑÈÄöÁü•
    recent: (count = 5) => {
      return computed(() => 
        notifications.value
          .slice()
          .sort((a, b) => b.timestamp - a.timestamp)
          .slice(0, count)
      )
    }
  }
  
  return {
    // Áä∂ÊÄÅ
    notifications,
    unreadCount,
    hasUnread,
    
    // Âü∫Á°ÄÊìç‰Ωú
    add: store.addNotification,
    remove: store.removeNotification,
    markAsRead: store.markNotificationAsRead,
    
    // ‰æøÊç∑ÊñπÊ≥ï
    notify,
    
    // ÊâπÈáèÊìç‰Ωú
    batch,
    
    // ËøáÊª§Âô®
    filter
  }
}